/**
 * Internal dependencies
 */
import domReady from '../utils/dom-ready';

// =====
// STATE
// =====

const queues: Record< string, ( () => void )[] > = {};
const triggeredEvents: string[] = [];

// =======
// KICKOFF
// =======

init();
function init() {
	if ( isIE() ) {
		addGlobalFunctions( 'fake' );
		return;
	} //end if

	maybeMarkAsStaging();

	hideContent();

	addSingleAction( 'valid-content', showValidContent );
	addTimeoutToShowContent( 2500 );
	addGlobalFunctions();
} //end init()

// =======
// HELPERS
// =======

function addTimeoutToShowContent( delay ) {
	domReady( () =>
		setTimeout( () => doSingleAction( 'valid-content' ), delay )
	);
} //end addTimeoutToShowContent()

function addGlobalFunctions( type?: 'fake' ): void {
	const noop = () => void null;
	/* eslint-disable @typescript-eslint/no-explicit-any, @typescript-eslint/no-unsafe-member-access */
	( window as any ).nabAddSingleAction =
		'fake' === type ? noop : addSingleAction;
	( window as any ).nabDoSingleAction =
		'fake' === type ? noop : doSingleAction;
	/* eslint-enable @typescript-eslint/no-explicit-any, @typescript-eslint/no-unsafe-member-access */
} //end addGlobalFunctions()

function isIE() {
	const ua = window.navigator.userAgent || '';
	return -1 !== ua.indexOf( 'MSIE ' ) || -1 !== ua.indexOf( 'Trident/' );
} //end isIE()

function hideContent() {
	const element = document.getElementById(
		'nab-alternative-loader-style'
	) as HTMLStyleElement;
	if ( ! element ) {
		return;
	} //end if

	const style =
		'html::before, body::before { background:#fff; content:""; width:100vw; height:100vh; position:fixed; z-index:999999999; }';
	element.innerHTML = '';
	element.appendChild( document.createTextNode( style ) );
} //end hideContent()

function addSingleAction( event: string, fn: () => void ) {
	try {
		if ( triggeredEvents.includes( event ) ) {
			fn();
			return;
		} //end if
		const queue = queues[ event ] || [];
		queue.push( fn );
		queues[ event ] = queue;
	} catch ( e ) {}
} //end addSingleAction()

function doSingleAction( event: string ) {
	if ( triggeredEvents.includes( event ) ) {
		return;
	} //end if
	triggeredEvents.push( event );

	const queue = queues[ event ] || [];
	queue.reverse();
	while ( queue.length ) {
		const fn = queue.pop();
		fn?.();
	} //end while
} //end doSingleAction

function showValidContent() {
	const element = document.getElementById( 'nab-alternative-loader-style' );
	if ( element?.parentNode ) {
		element.parentNode.removeChild( element );
	} //end if

	try {
		const event = document.createEvent( 'HTMLEvents' );
		event.initEvent( 'resize', true, false );
		document.dispatchEvent( event );
	} catch ( e ) {}
} //end showValidContent()

function maybeMarkAsStaging() {
	if ( ! /\bnabstaging\b/.test( document.location.search ) ) {
		return;
	} //end if

	document.cookie = 'nabIsStagingSite=true; path=/';
	if ( ! window.history?.replaceState ) {
		return;
	} //end if

	let url = document.location.href; // eslint-disable-line
	if ( /\bnabstaging&/.test( url ) ) {
		url = url.replace( 'nabstaging&', '' );
	} else if ( /&nabstaging\b/.test( url ) ) {
		url = url.replace( '&nabstaging', '' );
	} else {
		url = url.replace( '?nabstaging', '' );
	} //end if
	window.history.replaceState( {}, '', url );
} //end maybeMarkAsStaging()
